import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';

import { IonicModule } from '@ionic/angular';

import { FacturaspendientesPage } from './facturaspendientes.page';
import { ModalPage } from './modal/modal.component';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { HeaderModule } from '../shared/header/header.module';

const routes: Routes = [
  {
    path: '',
    component: FacturaspendientesPage
  }
];

@NgModule({
  imports: [
    CommonModule,
    NgxDatatableModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild(routes),
    HeaderModule
  ],
  declarations: [FacturaspendientesPage, ModalPage],
  entryComponents: [
    ModalPage
  ]
})
export class FacturaspendientesPageModule {}
