import { Component, OnInit, ViewChild } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ModalPage } from './modal/modal.component';
import { FacturaspagadasService } from '../facturaspagadas/facturaspagadas.service';
import { ToastService } from '../toast.service';

@Component({
  selector: 'app-facturaspendientes',
  templateUrl: './facturaspendientes.page.html',
  styleUrls: ['./facturaspendientes.page.scss'],
})
export class FacturaspendientesPage implements OnInit {
  @ViewChild('mydatatable') mydatatable: any;
  rows = [];
  filteredData;
  montoTotal = 0;
  moneda = '1';
  monedaSimbolo = '$';
  textoTabla = { emptyMessage: 'Cargando.... (Puede tardar un poco...)', totalMessage: 'Facturas' };

  constructor(public modalController: ModalController, private service: FacturaspagadasService, private toastService: ToastService) {
  }

  ngOnInit() {
    this.getData();
  }

  getData() {
    this.rows = [];
    this.textoTabla.emptyMessage = 'Cargando.... (Puede tardar un poco...)';
    this.mydatatable.messages = this.textoTabla;
    this.service.getFacturas(this.moneda).subscribe((data: any) => {
      if (data.length === 0) {
        this.textoTabla.emptyMessage = 'No hay datos para mostrar';
        this.mydatatable.messages = this.textoTabla;
      }
      if (this.moneda === '1') {
        this.monedaSimbolo = '$';
      } else {
        this.monedaSimbolo = 'USD';
      }
      this.rows = data;
      this.filteredData = [...data];
    }, error => {
      this.toastService.presentToast('Error al Recuperar los datos del servidor');
    });
  }

  changeMoneda(e) {
    // if (e.detail.checked === true) {
    //   this.moneda = '1';
    // } else {
    //   this.moneda = '2';
    // }
    this.montoTotal = 0;
    this.moneda = e.detail.value;
    this.getData();
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: ModalPage,
      componentProps: { value: 123 }
    });
    return await modal.present();
  }

  onSelect(e) {
    let monto = 0;
    if (e.selected.length > 1) {
      this.toastService.presentToast(e.selected.length + ' facturas seleccionadas.');
    } else if (e.selected.length == 1) {
      this.toastService.presentToast('Factura ' + e.selected['0'].numero_factura + ' seleccionada.');
    }
    e.selected.forEach(element => {
      monto = monto + Number(element.valor_total);
    });
    this.montoTotal = monto;
  }

  filterDatatable(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.filteredData.filter(d => {
      return d.numero_factura.toLowerCase().indexOf(val) !== -1 ||
        d.fecha_emision.toLowerCase().indexOf(val) !== -1 ||
        d.numero_factura.toLowerCase().indexOf(val) !== -1 ||
        d.valor_total.toLowerCase().indexOf(val) !== -1 ||
        !val;
    });

    // update the rows
    this.rows = temp;
    // Whenever the filter changes, always go back to the first page
    this.mydatatable.offset = 0;
  }

}
